    background: #e2dee8;
    border-top-left-radius: 100%;
    left: -36px;
    top: -16px;
    transform: rotate(-18deg) skewX(-20deg);
}

.ear {
    width: 40px;
    height: 46px;
    bottom: 0;
    transform: skewY(-10deg);
    transform-origin: right bottom;
}

.ear:before, .ear:after {
    content: "";
    display: block;
    position: absolute;
    height: 100%;
    width: 100%;
