body {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background: radial-gradient(circle at 50%, rgba(3, 40, 116, 0.9) 0%, rgba(20, 1, 26, 0.6) 100%), url("/src/resources/images/bg.webp");
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

html,
body {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
}

.header {
    overflow: hidden;
    margin-top: 70rem;
    width: 100vw;
}

.title > h1 {
    text-shadow: 0 0 4px rgba(255, 255, 255, 0.6);
    color: #fff;
}

.caption {
    width: 80%;
    margin: auto;
}

.caption > h1, h2, h3, h4 {
    color: #fefefe;
}

*,
*:before,
*:after {
    box-sizing: border-box;
    position: relative;
}

@media only screen and (max-width: 810px) {
    .header {
        margin-top: 110rem;
    }

    .fox {
        transform: scale(0.7);
    }
}
